import { BsArrowRight } from "react-icons/bs";
import { projects } from "../config";
import { Button, PortfolioItem } from "./parts";

const Portfolio = () => {
  return (
    <section id="portfolio" className="py-32">
      <div className="flex justify-between items-center mb-7 px-32">
        <div>
          <h1 className="text-4xl font-medium mb-3">
            See my latest <span className="text-primary">Project</span>
          </h1>
          <p className="text-lg font-light text-gray-300">
            Lorem ipsum dolor sit amet, consectetur adipisicing elit. Suscipit,
            eveniet!
          </p>
        </div>
        <Button
          link
          to="/portfolio"
          color="bg-primary"
          textColor="text-white"
          className="flex items-center gap-3"
        >
            See all <BsArrowRight size={34} />
        </Button>
      </div>
      <div className="overflow-x-auto no-scrollbar">
        <div className="flex gap-5 px-32 w-max">
          {projects.map((project) => (
            <PortfolioItem key={project.id} data={project} isRounded width="w-80" />
          ))}
        </div>
      </div>
    </section>
  );
};

export default Portfolio;
