import { Button } from "./parts"

const Contact = () => {
    return (
        <section id="contact" className="py-32 px-32">
            <div className="flex gap-16 items-center justify-center">
                <div className="flex-1">
                    <h1 className="text-4xl font-medium mb-3">Let's start <span className="text-primary">talking</span> with me</h1>
                    <p className="text-lg font-light text-gray-300">Lorem ipsum, dolor sit amet consectetur adipisicing elit. Alias repellat omnis veritatis saepe voluptatem repudiandae.</p>
                </div>
                <div className="flex-1">
                    <form className="p-6 rounded shadow w-96 flex flex-col items-start">
                        <input type="text" name="name" id="name" placeholder="Name" className="w-full p-3 rounded bg-gray-100 focus:ring-2 focus:ring-gray-200 focus:outline-none mb-3" />
                        <input type="email" name="email" id="email" placeholder="Email" className="w-full p-3 rounded bg-gray-100 focus:ring-2 focus:ring-gray-200 focus:outline-none mb-3" />
                        <textarea name="message" id="message" rows="4" placeholder="Message" className="w-full p-3 rounded bg-gray-100 focus:ring-2 focus:ring-gray-200 focus:outline-none mb-3" />
                        <Button href="#" color="bg-primary" textColor="text-white" className="py-3">Send message</Button>
                    </form>
                </div>
            </div>
        </section>
    )
}

export default Contact