import React from "react";
import { Link, useHistory } from "react-router-dom";
import { base_url, menus } from "../config";

const Header = (props, ref) => {
  const history = useHistory()

  return (
    <header
      ref={ref}
      className="fixed w-screen px-32 py-4 bg-black flex justify-between items-center animation duration-200"
      style={{ zIndex: 1 }}
    >
      <div className="text-2xl font-medium text-primary">
        <Link to="/">akbarputra</Link>
      </div>
      <nav className="flex justify-around gap-8">
        {menus.map((menu) => (
          <a
            key={menu.id}
            href={history.location.pathname !== '/' ? base_url+menu.url : menu.url}
            className="group font-light text-white"
          >
            {menu.title}
            <div className="bg-transparent w-3/5 h-1 group-hover:bg-primary group-focus:bg-primary transition duration-200" />
          </a>
        ))}
      </nav>
    </header>
  );
};

export default React.forwardRef(Header);
