import { Link } from "react-router-dom"

const Button = ({link = false, href, to, text, color, textColor, className, children}) => {
    return (
        link ? (
            <Link to={to} className={`px-5 py-1 rounded ${color} ${textColor} ${className}`}>{children}</Link>
        ) : (
            <a href={href} className={`px-5 py-1 rounded ${color} ${textColor} ${className}`}>{children}</a>
        )
    )
}

export default Button