import { useEffect } from "react";
import { Footer, Header} from "../components";
import { Button, PortfolioItem } from "../components/parts";
import { projects } from "../config";

const Portfolio = () => {
  useEffect(() => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  }, [])

  return (
    <>
      <Header />
      <section className="py-32 px-32">
          <div className="mb-10">
            <h1 className="text-4xl font-medium mb-3">
            My <span className="text-primary">portfolio</span>
            </h1>
            <p className="text-lg font-light text-gray-300">
            Lorem ipsum dolor sit amet, consectetur adipisicing elit. Suscipit,
            eveniet!
            </p>
          </div>
          <div className="flex mb-5">
              <Button href="#all" className="hover:bg-primary hover:text-white active:bg-primary active:text-white animation duration-500">All</Button>
              <Button href="#web" className="hover:bg-primary hover:text-white active:bg-primary active:text-white animation duration-500">Web</Button>
              <Button href="#mobile" className="hover:bg-primary hover:text-white active:bg-primary active:text-white animation duration-500">Mobile</Button>
              <Button href="#uiux" className="hover:bg-primary hover:text-white active:bg-primary active:text-white animation duration-500">UI/UX</Button>
          </div>
          <div className="flex flex-wrap">
                {projects.map((project) => (
                    <PortfolioItem key={project.id} data={project} width="w-3/12" />
                ))}
          </div>
      </section>
      <Footer/>
    </>
  );
};

export default Portfolio;
