import { stats } from "../config"

const Statistic = () => {
    return (
        <section className="flex justify-evenly items-center px-32 py-32">
            {stats.map(stat => (
                <div key={stat.id} className="flex flex-col items-center">
                    <h4 className="text-xl font-bold text-primary">{stat.title}</h4>
                    <p className="font-light text-gray-300">{stat.desc}</p>
                </div>
            ))}
        </section>
    )
}

export default Statistic